*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  // outline: 1px solid red;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

ul {
  list-style: none;
  padding: 0px;
  margin: 0px;
}

.Good {
  background-color: #008000;
}

.Neutral {
  background-image: linear-gradient(to right, green, red);
}

.Bad {
  background-color: red;
}
